___CSS_LOADER_EXPORT___.push([module.id, "\n.test-messagebar[data-v-230b6084] {\n  margin-bottom: 56px;\n}\n", "",{"version":3,"sources":["webpack://./front/src/pages/test/index.vue"],"names":[],"mappings":";AAgFA;EACA,mBAAA;AACA","sourcesContent":["<template>\n  <eb-page>\n    <eb-navbar large largeTransparent :title=\"$text('Test')\" eb-back-link=\"Back\"></eb-navbar>\n    <f7-messagebar ref=\"messagebar\" class=\"test-messagebar\" placeholder=\"Message\" @submit=\"onSubmitSendMessage\">\n      <f7-icon f7=\"::send\" slot=\"send-link\"></f7-icon>\n    </f7-messagebar>\n    <eb-list v-if=\"dd\" no-hairlines-md>\n      <eb-list-item title=\"钉钉扫一扫\" link=\"#\" :onPerform=\"onPerformScanQRCode\"></eb-list-item>\n      <eb-list-item title=\"获取MemberId\" link=\"#\" :onPerform=\"onPerformMemberId\"></eb-list-item>\n      <eb-list-item title=\"MemberId\">\n        <div slot=\"after\">{{ memberId }}</div>\n      </eb-list-item>\n    </eb-list>\n  </eb-page>\n</template>\n<script>\nexport default {\n  data() {\n    return {\n      dd: null,\n      memberId: null,\n    };\n  },\n  created() {\n    const action = {\n      actionModule: 'a-dingtalk',\n      actionComponent: 'jssdk',\n      name: 'config',\n    };\n    this.$meta.util\n      .performAction({ ctx: this, action })\n      .then(res => {\n        this.dd = res && res.dd;\n      })\n      .catch(e => {\n        this.$view.toast.show({ text: e.message });\n      });\n  },\n  mounted() {\n    this.messagebar = this.$refs.messagebar.f7Messagebar;\n  },\n  methods: {\n    onPerformScanQRCode() {\n      this.dd.biz.util.scan({\n        type: 'all',\n        onSuccess: res => {\n          this.$view.toast.show({ text: res.text });\n        },\n        onFail: err => {\n          this.$view.toast.show({ text: err.message });\n        },\n      });\n    },\n    onPerformMemberId() {\n      return this.$api.post('test/getMemberId').then(data => {\n        this.memberId = data.memberId;\n      });\n    },\n    async onSubmitSendMessage(value, clear) {\n      try {\n        // clear\n        clear();\n        // focus\n        if (value) {\n          this.messagebar.focus();\n        }\n        // send\n        await this.$api.post('test/sendAppMessage', {\n          message: {\n            text: value,\n          },\n        });\n      } catch (err) {\n        this.$view.toast.show({ text: err.message });\n      }\n    },\n  },\n};\n</script>\n<style scoped>\n.test-messagebar {\n  margin-bottom: 56px;\n}\n</style>\n"],"sourceRoot":""}]);
